import React, { useState } from "react";

export const ContentV1 = ({
  ctitle,
  stitle,
  bgcolor,
  bgimage,
  headlinecolor,
  linkcolor,
  pricev1,
  pricev2,
  bgarr,
  winwidth,
  num,
}) => {
  const resize_bg_c1 = (e) => {
    let width = window.innerWidth;
    let elem = [
      document.querySelector(
        ".content__container__v__cta__links__image__fig" + num
      ),
    ];
    if (width < winwidth[0])
      elem[0].style.backgroundImage = `url('${bgarr[1]}')`;
    if (width < winwidth[1])
      elem[0].style.backgroundImage = `url('${bgarr[2]}')`;
    if (width > winwidth[0])
      elem[0].style.backgroundImage = `url('${bgarr[0]}')`;
  };
  window.addEventListener("resize", resize_bg_c1);
  return (
    <React.Fragment>
      <div className="content__container" style={{ backgroundColor: bgcolor }}>
        <div className="content__container__v">
          <div className="content__container__v__d">
            <div className="content__container__v__d_i">
              <h2
                className="content__container__v__h"
                style={{ color: headlinecolor }}
              >
                {ctitle}
              </h2>
              <h2
                className="content__container__v__h2"
                style={{ color: headlinecolor }}
              >
                {stitle}
              </h2>
              <div className="content__container__v__pricing__info">
                <p className="price price-with-carrier">
                  <span>{pricev1}</span>
                </p>
                <p className="carrier price-with-carrier">{pricev2}</p>
              </div>
              <div className="content__container__v__cta__links">
                <span>
                  <a href="" style={{ color: linkcolor }}>
                    Learn more
                  </a>
                  <a href="" style={{ color: linkcolor }}>
                    Buy
                  </a>
                </span>
              </div>
            </div>
          </div>
        </div>
        <div className="content__container__v__cta__links__image">
          <figure
            style={{ backgroundImage: `url(${bgimage})` }}
            className={`content__container__v__cta__links__image__fig${num}`}
          ></figure>
        </div>
      </div>
    </React.Fragment>
  );
};

