export const ContentFooterLinks = ({ title, links, hasChildProp }) => {
  let [showed, setShowed] = useState(false);

  const show_title_ul = (e) => {
    let date_class = "node" + Date.now();
    e.target.parentNode.id = date_class;
    let ul_node = document.querySelector("#" + date_class).childNodes[1];
    let li_node = ul_node.childNodes;
    if (showed) {
      e.target.style.cssText = "border-bottom: 1px solid #d2d2d7;";
      ul_node.style.cssText =
        "height: 0px; border-bottom: none; padding-bottom: 0px";
      for (let i = 0; i < li_node.length; i++) {
        li_node[i].style.cssText = "padding-left: 0px";
      }
      setShowed(false);
    } else {
      e.target.style.cssText = "border-bottom: none;";
      ul_node.style.cssText =
        "height: auto; border-bottom: 1px solid #d2d2d7;  padding-bottom: 10px";
      for (let i = 0; i < li_node.length; i++) {
        li_node[i].style.cssText = "padding-left: 15px";
      }
      setShowed(true);
    }
  };
  return (
    <React.Fragment>
      <div className="footer__container__c">
        <p className="footer__container__c__title" onClick={show_title_ul}>
          {title}
        </p>
        <ul className="footer__container__c__title_ul">
          {links.map((link, i) => {
            return (
              <li className="footer__container__c__title_ul_li" key={i}>
                <a href="">{link}</a>
              </li>
            );
          })}
        </ul>
        <div className="footer__container__c_prop">
          {hasChildProp &&
            hasChildProp.map((childProp, i) => {
              return <React.Fragment key={i}>{childProp}</React.Fragment>;
            })}
        </div>
      </div>
    </React.Fragment>
  );
};

