import React, { useState } from "react";
import FeatherIcon from "feather-icons-react";
import "./Nav.css";
import "./BannerAlert.css";
import { setStyle } from "./js/fc";
import BannerAlert from "./BannerAlert";

const Nav = () => {
  const [nav__mobile_btn_clicked, set__nav__mobile_btn_clicked] = useState(
    false
  );

  window.addEventListener("resize", (e) => {
    if (window.innerWidth > 805) {
      set__nav__mobile_btn_clicked(false);
      setStyle(".nav__mobile__bar__1", {
        transform: "rotate(0deg)",
      });
      setStyle(".nav__mobile__bar__2", {
        transform: "rotate(0deg)",
      });

      setStyle(".nav__mobile__v", {
        height: "0px",
        width: "550px",
        margin: "auto",
        display: "block",
      });

      setStyle(".nav__mobile__v__search__links", {
        opacity: "0",
        transition: "500ms",
      });
      setTimeout(() => {
        setStyle(".nav__container", {
          "background-color": "#333",
        });
        document.querySelector(".nav__mobile__v").removeAttribute("style");
        setStyle(".nav__mobile__v__input", {
          opacity: "0",
        });
      }, 450);
      setTimeout(() => {
        setStyle(".nav__mobile__bar__1", {
          "margin-top": "2px",
        });
        setStyle(".nav__mobile__bar__2", {
          "margin-top": "10px",
        });
        const nav__mobile__bar = document.querySelectorAll(".nav__mobile__bar");
        for (let i = 0; i < nav__mobile__bar.length; i++) {
          nav__mobile__bar[i].removeAttribute("style");
        }
      }, 200);
      setStyle(".nav__mobile__v", {
        height: "0px",
      });
    }
  });
  const [
    nav__search__result__quick_links,
    setnav__search__result__quick_links,
  ] = useState(false);
  const nav__mobile_menu_click = () => {
    if (nav__mobile_btn_clicked) {
      set__nav__mobile_btn_clicked(false);
      setStyle(".nav__mobile__bar__1", {
        transform: "rotate(0deg)",
      });
      setStyle(".nav__mobile__bar__2", {
        transform: "rotate(0deg)",
      });

      setStyle(".nav__mobile__v", {
        height: "100vh",
      });
      setStyle(".nav__mobile__v__input", {
        opacity: "0",
      });

      setStyle(".nav__mobile__v__search__links", {
        opacity: "0",
        transition: "500ms",
      });
      setTimeout(() => {
        setStyle(".nav__container", {
          "background-color": "#333",
        });
      }, 450);
      setTimeout(() => {
        setStyle(".nav__mobile__bar__1", {
          "margin-top": "2px",
        });
        setStyle(".nav__mobile__bar__2", {
          "margin-top": "10px",
        });
        const nav__mobile__bar = document.querySelectorAll(".nav__mobile__bar");
        for (let i = 0; i < nav__mobile__bar.length; i++) {
          nav__mobile__bar[i].removeAttribute("style");
        }
      }, 200);
      setStyle(".nav__mobile__v", {
        height: "0px",
      });
    } else {
      set__nav__mobile_btn_clicked(true);
      setStyle(".nav__mobile__bar__1", {
        "margin-top": "5px",
      });
      setStyle(".nav__mobile__v__search__links", {
        opacity: "1",
        transition: "500ms",
      });
      setStyle(".nav__mobile__bar__2", {
        "margin-top": "5px",
      });
      setStyle(".nav__mobile__v__input", {
        opacity: "1",
      });
      setStyle(".nav__container", {
        "background-color": "#000",
      });
      setTimeout(() => {
        setStyle(".nav__mobile__bar__1", {
          transform: "rotate(45deg)",
        });
        setStyle(".nav__mobile__bar__2", {
          transform: "rotate(-45deg)",
        });
      }, 200);
      setStyle(".nav__mobile__v", {
        height: "100vh",
      });
    }
  };

  const scale_nav_links = () => {
    const nav__links__li = document.querySelectorAll(".nav__links__li");
    const nav__links__li_a = document.querySelectorAll(".nav__links__li_a");
    const nav__search__result__quick_links__li = document.querySelectorAll(
      ".nav__search__result__quick_links__li"
    );

    let ct = 0;
    let ctt = 0;
    let ct0 = 0;
    let cttt = 0;

    if (!nav__search__result__quick_links) {
      setStyle(".nav__container", {
        "background-color": "#1b1b1b",
        transition: "800ms",
      });

      for (let i = 0; i < nav__links__li.length; i++) {
        nav__links__li[i].id = "nav__links__li_" + i;
        nav__links__li_a[i].id = "nav__links__li_a" + i;
        ctt++;
      }

      for (let i = ctt - 1; i >= 0; i--) {
        ct += 50;
        setTimeout(() => {
          setStyle("#nav__links__li_" + i, {
            opacity: 0,
          });
          setStyle("#nav__links__li_a" + i, {
            transform: "scale(0.7)",
          });
        }, ct);
      }

      for (let i = 0; i < nav__search__result__quick_links__li.length; i++) {
        nav__search__result__quick_links__li[i].id =
          "nav__search__result__quick_links__li" + i;
        cttt++;
      }

      setTimeout(() => {
        setStyle(".nav__search__results", {
          opacity: "1",
          visibility: "visible",
        });
        setStyle(".nav__search__container", {
          visibility: "visible",
          opacity: 1,
        });
      }, ct - 50);
      setTimeout(() => {
        setStyle(".nav__search__input__field", {
          left: "30px",
          opacity: 1,
        });
        setStyle(".nav__search__input__icon", {
          left: "0px",
          opacity: 1,
        });
      }, ct);
      setTimeout(() => {
        for (let i = 0; i < cttt; i++) {
          ct0 += 60;
          setTimeout(() => {
            setStyle("#nav__search__result__quick_links__li" + i, {
              left: "0px",
            });
            setTimeout(() => {
              setStyle("#nav__search__result__quick_links__li" + i, {
                opacity: "1",
              });
            }, ct0 - 400);
          }, ct0);
        }
        setStyle(".nav__container__overlay", {
          opacity: ".5",
          "pointer-events": "auto",
          "z-index": 10,
        });
      }, ct - 200);
      setStyle("body", {
        overflow: "hidden",
      });
      setnav__search__result__quick_links(true);
    } else {
      setStyle(".nav__container__overlay", {
        opacity: "0",
      });
      setStyle(".nav__search__results", {
        opacity: "0",
        visibility: "none",
      });
      setStyle(".nav__search__container", {
        visibility: "none",
        opacity: 0,
      });
      setStyle(".nav__search__input__field", {
        left: "70px",
        opacity: 0,
      });
      setStyle(".nav__search__input__icon", {
        left: "40px",
        opacity: 0,
      });
      setStyle(".nav__container", {
        "background-color": "#333",
        transition: "1000ms",
      });

      for (let i = 0; i < nav__links__li.length; i++) {
        nav__links__li[i].id = "nav__links__li_" + i;
        nav__links__li_a[i].id = "nav__links__li_a" + i;
        ctt++;
      }

      for (let i = 0; i < ctt; i++) {
        ct += 50;
        setTimeout(() => {
          setStyle("#nav__links__li_" + i, {
            opacity: 1,
          });
          setStyle("#nav__links__li_a" + i, {
            transform: "scale(1)",
          });
        }, ct);
      }

      for (let i = 0; i < nav__search__result__quick_links__li.length; i++) {
        nav__search__result__quick_links__li[i].removeAttribute("style");
      }
      document.body.removeAttribute("style");
      setnav__search__result__quick_links(false);
    }
  };
  const nav__mobile__v__search__input__field = () => {
    const w_width = window.innerWidth;
    setStyle(".nav__container", {
      "margin-top": "-36px",
    });
    setStyle(".nav__mobile__v__search__input__field", {
      "margin-right": "70px",
    });
    setStyle(".nav__mobile__v__search__input__field__cancel", {
      opacity: "1",
    });
    setStyle(".nav__mobile__v__search__links", {
      "margin-top": "80px",
    });
    setStyle(".nav__mobile__v__search__quick", {
      "margin-top": "0px",
      transition: "370ms",
    });
    setTimeout(() => {
      setStyle(".nav__mobile__v__search__links", {
        opacity: "0",
        visibility: "hidden",
      });
      setStyle(".nav__mobile__v__search__quick", {
        opacity: "1",
      });
    }, 20);
  };
  const nav__mobile__v__search__input__field__cancel = () => {
    setStyle(".nav__container", {
      "margin-top": "0px",
    });

    document
      .querySelector(".nav__mobile__v__search__input__field")
      .removeAttribute("style");
    setStyle(".nav__mobile__v__search__input__field__cancel", {
      opacity: "0",
    });
    setStyle(".nav__mobile__v__search__links", {
      "margin-top": "0px",
    });
    setStyle(".nav__mobile__v__search__quick", {
      "margin-top": "-70px",
      transition: "250ms",
    });
    setTimeout(() => {
      setStyle(".nav__mobile__v__search__links", {
        opacity: "1",
        visibility: "visible",
      });
      setStyle(".nav__mobile__v__search__quick", {
        opacity: "0",
      });
    }, 20);
  };
  return (
    <React.Fragment>
      <div className="nav__container__overlay" onClick={scale_nav_links}></div>
      <nav className="nav__container">
        <div className="nav__search__container">
          <div className="nav__search__input__contain">
            <div className="nav__search__input__container">
              <FeatherIcon
                className="nav__search__input__icon"
                icon="search"
                size="20"
              />
              <input
                className="nav__search__input__field"
                type="text"
                placeholder="Search apple.com"
              />
              <div className="nav__search__close">
                <div
                  className="nav__search__close__btn"
                  onClick={scale_nav_links}
                ></div>
                <div className="nav__search__close__line nav__search__close__line__1"></div>
                <div className="nav__search__close__line nav__search__close__line__2"></div>
              </div>
            </div>
            <div className="nav__search__results">
              <div className="nav__search__results__links">
                <div className="nav__search__result__quick">
                  <p className="nav__search__result__quick_title nav__search__result__quick_links__li">
                    Quick links
                  </p>
                  <ul className="nav__search__result__quick_links">
                    <li className="nav__search__result__quick_links__li">
                      <a href="" className="nav__search__result__quick_links_a">
                        COVID-19 Information
                      </a>
                    </li>
                    <li className="nav__search__result__quick_links__li">
                      <a href="" className="nav__search__result__quick_links_a">
                        Apple Store Closure FAQ
                      </a>
                    </li>
                    <li className="nav__search__result__quick_links__li">
                      <a href="" className="nav__search__result__quick_links_a">
                        Shop Apple Store Online
                      </a>
                    </li>
                    <li className="nav__search__result__quick_links__li">
                      <a href="" className="nav__search__result__quick_links_a">
                        Accessories
                      </a>
                    </li>
                    <li className="nav__search__result__quick_links__li">
                      <a href="" className="nav__search__result__quick_links_a">
                        AirPods
                      </a>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="nav__container-child">
          <div className="nav__mobile">
            <div
              className="nav__mobile_btn"
              onClick={nav__mobile_menu_click}
            ></div>
            <div className="nav__mobile__bar nav__mobile__bar__1"></div>
            <div className="nav__mobile__bar nav__mobile__bar__2"></div>
          </div>
          <div className="nav__logo nav__links__li nav__logo nav__links__m">
            <img
              className="nav__logo__img nav__links__li_a"
              src="https://www.apple.com/ac/globalnav/6/en_US/images/be15095f-5a20-57d0-ad14-cf4c638e223a/globalnav_apple_image__cxwwnrj0urau_large.svg"
            />
          </div>
          <div className="nav__links">
            <ul>
              <li className="nav__links__li">
                <a href="" className="nav__links__li_a">
                  Mac
                </a>
              </li>
              <li className="nav__links__li">
                <a href="" className="nav__links__li_a">
                  iPad
                </a>
              </li>
              <li className="nav__links__li">
                <a href="" className="nav__links__li_a">
                  iPhone
                </a>
              </li>
              <li className="nav__links__li">
                <a href="" className="nav__links__li_a">
                  Watch
                </a>
              </li>
              <li className="nav__links__li">
                <a href="" className="nav__links__li_a">
                  TV
                </a>
              </li>
              <li className="nav__links__li">
                <a href="" className="nav__links__li_a">
                  Music
                </a>
              </li>
              <li className="nav__links__li">
                <a href="" className="nav__links__li_a">
                  Support
                </a>
              </li>
              <li className="nav__links__li">
                <a
                  className="nav__search__btn__l nav__links__li_a"
                  onClick={scale_nav_links}
                >
                  <FeatherIcon icon="search" size="20" />
                </a>
              </li>
              <li className="nav__links__li">
                <a href="" className="nav__links__li_a">
                  <FeatherIcon icon="credit-card" size="20" />
                </a>
              </li>
            </ul>
          </div>
          <div className="nav__mobile">
            <FeatherIcon icon="credit-card" size="20" />
          </div>
        </div>
        {/* mobile menu */}
        <div className="nav__mobile__v">
          <div className="nav__mobile__v__d">
            <div className="nav__mobile__v__input">
              <FeatherIcon
                className="nav__mobile__v__input__icon"
                icon="search"
                size="23"
              />
              <input
                className="nav__mobile__v__search__input__field"
                onClick={nav__mobile__v__search__input__field}
                type="text"
                placeholder="Search apple.com"
              />
              <button
                className="nav__mobile__v__search__input__field__cancel"
                onClick={nav__mobile__v__search__input__field__cancel}
              >
                Cancel
              </button>
            </div>
          </div>
          <div className="nav__mobile__v__search__results__r"></div>
          <div className="nav__mobile__v__search__results_m">
            <div className="nav__mobile__v__search__results nav__mobile__v__search__quick">
              <div className="nav__mobile__v__search__results__links nav__mobile__v__search__quick__l">
                <p className="nav__mobile__v__search__quick__title">
                  Quick links
                </p>
                <ul>
                  <li>
                    <a href="">COVID-19 Information</a>
                  </li>
                  <li>
                    <a href="">Apple Store Closure FAQ</a>
                  </li>
                  <li>
                    <a href="">Shop Apple Store Online</a>
                  </li>
                  <li>
                    <a href="">Accessories</a>
                  </li>
                  <li>
                    <a href="">AirPods</a>
                  </li>
                </ul>
              </div>
            </div>
            <div className="nav__mobile__v__search__results nav__mobile__v__search__links ">
              <div className="nav__mobile__v__search__results__links">
                <ul>
                  <li className="">
                    <a href="">Mac</a>
                  </li>
                  <li>
                    <a href="">iPad</a>
                  </li>
                  <li>
                    <a href="">iPhone</a>
                  </li>
                  <li>
                    <a href="">Watch</a>
                  </li>
                  <li>
                    <a href="">TV</a>
                  </li>
                  <li>
                    <a href="">Music</a>
                  </li>
                  <li>
                    <a href="">Support</a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </nav>
      {/* banners */}
      <BannerAlert
        details={[
          <React.Fragment key={Date.now()}>
            <a href="">Evaluate COVID‑19 symptoms and understand next steps.</a>
          </React.Fragment>,
        ]}
        bgcolor="#0071e3"
        txtpadding="120"
      />
      <BannerAlert
        details={[
          <React.Fragment key={Date.now()}>
            <a href="" style={{ color: "#06c" }}>
              Shop online
            </a>{" "}
            <span style={{ color: "#1d1d1f" }}>
              and get Specialist help, free no-contact delivery, and more.
            </span>
          </React.Fragment>,
        ]}
        bgcolor="#f5f5f7"
        txtpadding="40"
      />
    </React.Fragment>
  );
};

export default Nav;
