import React from "react";

const BannerAlert = ({ details, bgcolor, txtpadding }) => {
  return (
    <div
      className="banner__alert__container"
      style={{ backgroundColor: bgcolor }}
    >
      <span
        style={{ padding: `0px ${txtpadding}px 0px ${txtpadding}px` }}
        href=""
        className="banner__alert__link"
      >
        {details}
      </span>
    </div>
  );
};

export default BannerAlert;
