import React, { useState } from "react";
import "./content.css";
import Nav from "./Nav";
import * as content_d from "./js/content_d";
import * as footer_l from "./js/footer_l";
import {
  ContentV1,
  ContentV2,
  ContentV3,
  ContentV4,
  ContentFooterPrivacy,
  ContentFooterLinks,
  Footer_Copyright,
} from "./Content";

function App() {
  return (
    <div className="App">
      <Nav />
      <div className="content__holder">
        <ContentV1
          ctitle="iPhone 12"
          stitle="Blast past fast"
          bgcolor="#fbfbfd"
          headlinecolor="#1d1d1f"
          linkcolor="#06c"
          pricev1="From $29.12/mo. or $699 before trade‑in."
          pricev2="Buy directly from Apple with special carrier offers."
          bgimage={content_d.bgimage}
          bgarr={content_d.bgarr}
          winwidth={content_d.winwidth}
          num={1}
        />
        <ContentV1
          ctitle="iPhone 12 Pro"
          stitle="Blast past fast"
          bgcolor="#000000"
          headlinecolor="#fff"
          linkcolor="#2997ff"
          pricev1="From $41.62/mo. or $999 before trade‑in."
          pricev2="Buy directly from Apple with special carrier offers."
          bgimage={content_d.bgimage2}
          bgarr={content_d.bgarr2}
          winwidth={content_d.winwidth}
          num={2}
        />
        <ContentV2
          ctitle="iPad Air"
          stitle="Powerful. Colorful. Wonderful"
          bgcolor="#fbfbfd"
          headlinecolor="#1d1d1f"
          linkcolor="#06c"
          pricev1=""
          pricev2=""
          bgimage={content_d.bgimage3}
          bgarr={content_d.bgarr3}
          winwidth={content_d.winwidth}
          num={3}
        />
        <ContentV3
          ctitle="WATCH"
          ctitle2="MacBook Air"
          stitle="The future of health in on your wrist"
          stitle2="Power. It's in the Air"
          bgcolor="#000000"
          bgcolor2="#000000"
          headlinecolor="#fff"
          linkcolor="#2997ff"
          pricev1=""
          pricev2=""
          bgimage={content_d.bgimage4}
          bgimage2={content_d.bgimage5}
          bgarr={content_d.bgarr4}
          bgarr2={content_d.bgarr5}
          winwidth={content_d.winwidth}
          num={4}
        />
        <ContentV3
          ctitle="Fitness"
          ctitle2="One"
          stitle="A new fitness experience for everyone. Powered by Apple Watch."
          stitle2="Bundle up to six Apple services. And enjoy more for less."
          stitlepaddinglr="50px"
          bgcolor="#fbfbfd"
          bgcolor2="#fbfbfd"
          mainbgcolor="#fbfbfd"
          headlinecolor="#000"
          linkcolor="#2997ff"
          pricev1=""
          pricev2=""
          bgimage={content_d.bgimage6}
          bgimage2={content_d.bgimage7}
          bgarr={content_d.bgarr6}
          bgarr2={content_d.bgarr7}
          winwidth={content_d.winwidth}
          num={5}
        />
        <ContentV4
          ctitle=""
          ctitle2="One"
          stitle="Save 6% on Apple products with a new Apple Card — all March long.*"
          stitle2="Bundle up to six Apple services. And enjoy more for less."
          stitlepaddinglr="50px"
          bgcolor="#fbfbfd"
          bgcolor2="#fbfbfd"
          mainbgcolor="#fbfbfd"
          headlinecolor="#000"
          linkcolor="#2997ff"
          pricev1=""
          pricev2=""
          bgimage={content_d.bgimage8}
          bgimage2={content_d.bgimage9}
          bgimage3={content_d.bgimage10}
          bgarr={content_d.bgarr8}
          bgarr2={content_d.bgarr9}
          bgarr3={content_d.bgarr10}
          winwidth={content_d.winwidth}
          num={6}
        />
        <ContentFooterPrivacy />
        <div className="footer__container__links">
          <div className="footer__container__links__child">
            <ContentFooterLinks
              title="Shop and Learn"
              links={footer_l.links1}
            />
            <ContentFooterLinks
              title="Services"
              links={footer_l.links2}
              hasChildProp={[
                <ContentFooterLinks title="Account" links={footer_l.links2s} />,
              ]}
            />
            <ContentFooterLinks title="Apple Store" links={footer_l.links3} />
            <ContentFooterLinks
              title="For Business"
              links={footer_l.links4}
              hasChildProp={[
                <ContentFooterLinks
                  title="For Education"
                  links={footer_l.links4s}
                />,
                <ContentFooterLinks
                  title="For Healthcare"
                  links={footer_l.links4s2}
                />,
                <ContentFooterLinks
                  title="For Government"
                  links={footer_l.links4s3}
                />,
              ]}
            />
            <ContentFooterLinks
              title="Apple Values"
              links={footer_l.links5}
              hasChildProp={[
                <ContentFooterLinks
                  title="About Apple"
                  links={footer_l.links5s}
                />,
              ]}
            />
          </div>
          <Footer_Copyright />
        </div>
      </div>
    </div>
  );
}

export default App;
